<template>
  <div>
      <div><img :src="src"></div>
       <vue-core-image-upload 
            :class="['el-button','pure-button-primary','js-btn-crop']"
            crop="local"
            text=""
            :url="url"
            :cropRatio="cropRatio"
            :inputOfFile="filename"
            extensions="png,gif,jpeg,jpg"
            @imageuploaded="imageuploaded"
            @errorhandle="handleError">
        </vue-core-image-upload>
  </div>
</template>

<script>
import VueCoreImageUpload from 'vue-core-image-upload'
export default {
    data(){
        return {
            src:'',
        }
    },
    components:{
        VueCoreImageUpload
    },
    props:{
        url:{
            type:String,
            default:'/alert/upload'

        },
        filename:{
            type:String,
            default:'file'
        },
         cropRatio:{
            type:String,
            default:'1:1'
         }
    },
    methods:{
        imageuploaded(res) {
            console.log(res)
            this.src = res[0]
            this.$emit('getImg',this.src)
            
        },
        handleError(){
            this.$notify.error({
                title: '',
                message: ''
            });
        }
    }
}
</script>

<style>
  
</style>



